import React from "react";

import "./styles.css"
import { getStore, parseDateMMDDYYYY } from "../../../stores/Utils";
import { NavLink, useNavigate } from "react-router-dom";
import { CloseModal } from "../../../components/DialogHRM";



const ViewComprobante  = ({ data, item, accountForm }) => {
	

	let accountD = getStore("account_ids")?.[getStore(accountForm)?.account];
	let subaccountD = getStore("sub_account_ids")?.[getStore(accountForm)?.subAccount];


	const navigate = useNavigate();

	const close =  (element) => {
		CloseModal({id:data.modalID});
		navigate({
			pathname: "accounting-ledger" ,
			search: `?cId=${element.comprobanteId}`
		});
	}

	
	
	return (
		<div style={{
			maxHeight: "80vh",
			width: "560px",
			overflow: "auto",
			background: "var(--hrm_palette-general-background-b)",
			borderRadius: 13,
			padding: "10px 19px 30px"
		}}>
			<div className="_dsplFlx ">
				<p className="title_collect">
					{ "Movimientos de la Cuenta"}
				</p>
			</div>

			<div className=" acc_dtls" style={{marginTop: 10}}>
				<div className="_dsplFlx"  >
					<div className="code" style={{color: "#646cff"}}>
						{accountD?.code} 
					</div>
					<div className="name">
						{accountD?. name}
					</div>
				</div>
				<div className="flexSpace"/>
				{subaccountD ?
				<div className="_dsplFlx"  style={{margin: "10px 0 0 18px"}}>
					<div className="code" >
						{subaccountD?.subAccount} 
					</div>
					<div className="name">
						{subaccountD?. subAccountName}
					</div>
				</div>
				:null}
			</div>
			<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<p className="title_collect">
					{ "En el documento"}
				</p>
				<p className="form_title" >
				{item?. document}
				</p>
			</div>
			
			<div className="_dsplFlx cmp_dtls">
				
				<div className="flexSpace"/>
				<div className="_dsplFlx"  >
					
				</div>
			</div>




			
			<div className="_dsplFlx hdr_bar purple">
				<div className="tr_bar tr_bar_25">
					Comprobante
				</div>
				<div className="tr_bar tr_bar_25">
					Fecha
				</div>
				<div className="tr_bar tr_bar_25">
					Debito
				</div>
				<div className="tr_bar tr_bar_25">
					Credito
				</div>
			</div>
			

			{item?.operations?.map((element, ind43) => {
				return(
					<div className="_dsplFlx hdr_bar" key={ind43}>
						<div className="tr_bar tr_bar_25 comprobante_id" onClick={()=>close(element)}>
								{element.comprobanteId}
						</div>
						<div className="tr_bar tr_bar_25">
							{element.date && parseDateMMDDYYYY(element.date)}
						</div>
						<div className="tr_bar tr_bar_25">
							{(element.debit*1)?.toFixed(2)}
						</div>
						<div className="tr_bar tr_bar_25">
							{(element.credit*1)?.toFixed(2)}
						</div>
					</div>
				)
			})}

		</div>
	);
};

export default ViewComprobante;
